'use client';

/**
 * PWA Provider Component
 *
 * Registers service worker and manages PWA functionality
 */

import { useEffect, useState } from 'react';
import { registerServiceWorker, listenForOnlineStatus, isOnline } from '@/lib/pwa';
import { InstallPrompt } from './InstallPrompt';

export function PWAProvider({ children }: { children: React.ReactNode }) {
  const [online, setOnline] = useState(true);
  const [showOfflineBanner, setShowOfflineBanner] = useState(false);

  useEffect(() => {
    // Register service worker
    registerServiceWorker();

    // Set initial online status
    setOnline(isOnline());

    // Listen for online/offline events
    const cleanup = listenForOnlineStatus(
      () => {
        console.log('[PWA] Back online');
        setOnline(true);
        setShowOfflineBanner(false);
      },
      () => {
        console.log('[PWA] Gone offline');
        setOnline(false);
        setShowOfflineBanner(true);
      }
    );

    return cleanup;
  }, []);

  return (
    <>
      {children}

      {/* Offline Banner */}
      {showOfflineBanner && (
        <div className="fixed top-0 left-0 right-0 bg-yellow-500 text-white px-4 py-2 text-center text-sm font-medium z-50 shadow-lg">
          <div className="flex items-center justify-center gap-2">
            <svg className="h-5 w-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M18.364 5.636a9 9 0 010 12.728m0 0l-2.829-2.829m2.829 2.829L21 21M15.536 8.464a5 5 0 010 7.072m0 0l-2.829-2.829m-4.243 2.829a4.978 4.978 0 01-1.414-2.83m-1.414 5.658a9 9 0 01-2.167-9.238m7.824 2.167a1 1 0 111.414 1.414m-1.414-1.414L3 3m8.293 8.293l1.414 1.414" />
            </svg>
            <span>You're offline. Don't worry, your data will sync when you reconnect.</span>
          </div>
        </div>
      )}

      {/* Install Prompt */}
      <InstallPrompt />
    </>
  );
}
